.sidebar-container {
    position: relative;
    background-color: #f0f5f3;
    /* width: 1440px; */
    height: 1024px;
}

.whiteRound {
    position: absolute;
    top: 10px;
    left: 20px;
    background-color: white;
    border-radius: 32px;
    width: 65px;
    height: 65px;
}

.sideBar {
    position: absolute;
    top: 90px;
    left: 20px;
    background-color: white;
    border-radius: 50px;
    width: 65px;
    height: 920px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon {
    position: absolute;
    border-radius: 50px;
    background-color: #f0f5f3;
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.icon.active{
    background-color: #d9d9d9;
}

.icon.last{
    background-color: #f0f5f3;
    bottom: 10px;
}